* {
    box-sizing: border-box; }
@mixin button-click {
    margin-bottom: 30px;
    border-style: none;
    background-color: #dadada;
    border-radius: 5px;
    height: 30px; }
@mixin tumbler {
    .switch {
        position: relative;
        display: inline-block;
        width: 48px;
        height: 28px; }
    .switch input {
        opacity: 0;
        width: 0;
        height: 0; }
    .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #ccc;
        -webkit-transition: .3s;
        transition: .3s; }
    .slider:before {
        position: absolute;
        content: "";
        height: 20px;
        width: 20px;
        left: 3px;
        bottom: 4px;
        background-color: white;
        -webkit-transition: .3s;
        transition: .3s; }
    input:checked + .slider {
        background-color: #2196F3; }
    input:focus + .slider {
        box-shadow: 0 0 1px #2196F3; }
    input:checked + .slider:before {
        -webkit-transform: translateX(20px);
        -ms-transform: translateX(20px);
        transform: translateX(20px); }
    .slider.round {
        border-radius: 34px; }
    .slider.round:before {
        border-radius: 50%; } }




